import {
	mapState
} from 'vuex'
import {
	regExp
} from '@/common/utils.js'
import countDown from '@/components/countdown/index'
export default {
	components: {
		countDown
	},
	data() {
		return {
			stateBool:false,
			loginWayBool:true,
			mobile: '',
			code: '',
			phone:'',
			password:'',
		}
	},
	computed: {
		...mapState(['userInfo'])
	},
	onLoad() {

	},
	methods: {
		//切换登录方式
		changeLoginWay(){
			this.loginWayBool = !this.loginWayBool
			this.mobile = ''
			this.code = ''
			this.phone = ''
			this.password = ''
		},
		//去注册
		registerBtn(){
			uni.navigateTo({
				url:'/pages/login/register'
			})
		},
		//点击获取验证码
		getCodeBtn(){
			if(!this.$refs.countDown.stateBool()){
				if (regExp('mobile', this.mobile)) {
					this.$refs.countDown.sendCode()
					this.$http({
						api: '/koala/v1/verificationCodes',
						method: 'POST',
						params: {
							phone: this.mobile,
							verification_type:'login'
						}
					}).then(res => {
						// let getNum = res.data.message.replace(/[^0-9]/ig,"")
						// this.nowCode = Number(getNum)
					}).catch(err => {
				
					})
				} else {
					uni.showToast({
						icon: 'none',
						title: '请输入正确的手机号'
					})
				}
			}
		},
		//验证码登录
		codeLogin() {
			this.judgeFun().then(() => {

			}).catch(err => {})
		},
		//验证码/密码登录判断
		judgeFun() {
			return new Promise((resolve, reject) => {
				let tip = ''
				if(this.loginWayBool){
					if (!regExp('mobile', this.mobile)) {
						tip = '请输入正确的手机号'
						reject()
					} else if (!this.code) {
						tip = '请输入验证码'
						reject()
					} else {
						resolve()
					}
				}else{
					if (!regExp('mobile', this.phone)) {
						tip = '请输入正确的手机号'
						reject()
					} else if (!this.password) {
						tip = '请输入密码'
						reject()
					} else {
						resolve()
					}
				}
				if (tip) {
					uni.showToast({
						icon: 'none',
						title: tip
					})
				}
			})
		},
		//密码登录
		passwordLogin(){
			this.judgeFun().then(() => {
			
			}).catch(err => {})
		},
		//密码登录验证
		
		// 微信登录
		wxLogin(){
			this.appwxLogin()
		},
		//app微信授权登录
		appwxLogin() {
			uni.getProvider({
				service: 'oauth',
				success: res => {
					if (~res.provider.indexOf('weixin')) {
						uni.login({
							provider: 'weixin',
							success: loginRes => {
								console.log('-------获取openid(unionid)-----');
								console.log(JSON.stringify(loginRes));
								// 获取用户信息
								uni.getUserInfo({
									provider: 'weixin',
									success: infoRes => {
										console.log(infoRes, 'i')
										this.$store.commit('setUserInfo', infoRes.userInfo)
										uni.navigateBack()
									}
								});
							}
						});
					}
				}
			});
		},
	}
}
